import React from 'react'
import {Loader} from "./Loader";

export const Profile = ({userData}) => {

    if (!userData) {
        return <Loader/>
    }

    return (
        <div className="container-fluid">

            <div className="card border-dark mb-3">
                <div className="card-header"></div>
                <div className="card-body text-dark">
                    <h5 className="card-title">'</h5>
                    <p className="card-text">{userData.firstName}</p>
                    <hr/>
                    <h5 className="card-title"></h5>
                    <p className="card-text">{userData.lastName}</p>
                    <hr/>
                    <h5 className="card-title">Email</h5>
                    <p className="card-text">{userData.email}</p>
                    {userData.status === 'student' ?
                        <>
                            <hr/>
                            <h5 className="card-title"></h5>
                            {userData.speciality ? <p className="card-text">{userData.speciality.specialityCode}</p> : <p> </p>}
                            <hr/>
                            <h5 className="card-title"> </h5>
                            {userData.mentor ?
                                <p className="card-text"> {userData.mentor.scienceTitle} {userData.mentor.firstName} {userData.mentor.lastName} </p>
                                : <p> </p>}
                            <hr/>
                            <h5 className="card-title"> </h5>
                            <p className="card-text"> {userData.admissionResult === true ? '' : userData.admissionResult === false ? '' : '  '} </p>
                        </>
                        : ''}
                </div>
            </div>

        </div>
    )
}